@import url(https://fonts.googleapis.com/css?family=Lato|Permanent+Marker);
@import url(https://fonts.googleapis.com/css?family=PT+Sans:700);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.note {
  padding:10px 10px 38px 34px;
  height: auto;
  margin: auto;
  margin-bottom: 8px;
  width: 60%;
  font-family: 'Lato', sans-serif;
  font-size: 1.4em;
  color: #222;
  background-color: #eed;
  border-radius: 3px;
  box-shadow: 43x 3px 2px #333;
  opacity: 1;
}

.timeBox {
  width: 50%;
  float: right;
}

.buttonContainer {
  width: 100%;
  height: 15px;
  display: block;
}

.userBox {
  width: 50%;
  float: left;
}

.utContainer {
  width: 100%;
}

.makeRight {
  float: right;
}

@-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }

@keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
@-webkit-keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
@keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }

.fade-in {
    opacity:0;
    -webkit-animation:fadeIn ease-in 1;
            animation:fadeIn ease-in 1;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
    -webkit-animation-duration: 0.1s;
            animation-duration: 0.1s;
}

.tweetContent{
  float:left;
  padding-right: 70px;
  padding-bottom: 10px;
  display: block;
}

.userDisplay, .timeStamp{
  font-size: 20px;
}

.closebtn {
    color: #AA9;
    font-weight: bold;
    float: right;
    cursor: pointer;
    font-size: 1.3em;
    line-height: 10px;
}

.closebtn:hover {
    color: #AC2500;
}

.formWrapper{
    margin: auto;
    height: 40px;
}

.tweetButton, .filterButton{
    border: none;
    margin: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: 8px 24px;
    font-size: 1.3em;
    font-family: 'Droid Sans', sans-serif;
    background-color: #AEDAFF;
    color: rgba(0,0,0,0.5);
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.tweetButton:hover, .filterButton:hover{
    background-color: #B3FAEA;
    color: rgba(0,0,0,0.5);
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.tweetInput, .usernameInput{
    width: 800px;
    min-width: 800px;
    font-size: 1.3em;
    padding-left: 20px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-family: 'Droid Sans', sans-serif;
    background-color: rgba(34,49,63,0.8);
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.tweetInput:focus{
    background-color: #E4F1FE;
    color: #111;
    -webkit-transition: 0.2s ease-in;
    transition: 0.2s ease-in;
}

html{
  height: 100%;
}

body {
  width: 100%;
  min-height:100%;
  background: #B19BD9;
  background-attachment: fixed;
  background-size: cover;
}

* {
  height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

.tweetsFooter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.tweetsHeader {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 5%;
          flex: 1 1 5%;
  min-height: 2.5em;
  max-height: 2em;
  font-size: 3.2em;
  color: white;
  font-family: 'PT Sans', cursive;
  text-align: center;
  -webkit-align-content: center;
          align-content: center;
  padding:20px 0px 20px 0px;
  margin-bottom: 6px;
  background-color: rgba(0,0,0,0.5);
  text-shadow: 3px 3px 10px #000;
  display: block;
}

.tweetsBody {
  padding-top: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 85%;
          flex: 1 1 85%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.tweetsFooter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 5%;
          flex: 1 1 5%;
  background-color: #B19BD9;
}

.authentication, .userButton{
  border: none;
  margin: 0px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 0px;
  font-size: 0.4em;
  font-family: 'Droid Sans', sans-serif;
  background-color: #AEDAFF;
  color: rgba(0,0,0,0.5);
  -webkit-transition: 0.2s ease-out;
  transition: 0.2s ease-out;
  width: 100ox;
  height: 30px;
}

.authentication:hover, .userButton:hover{
    background-color: #B3FAEA;
    color: rgba(0,0,0,0.5);
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.login, .userButton{
  float:right;
}

.userButton{
  width:15%;
  height:50px;
  font-size:20px;
}

